"use client"

import { useState, useEffect } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Heart, X } from "lucide-react"

interface SwipeCardProps {
  profile: {
    id: string
    name: string
    age: number
    bio: string
    image: string
  }
  onSwipe: (direction: "left" | "right") => void
}

export function SwipeCard({ profile, onSwipe }: SwipeCardProps) {
  const [dragStart, setDragStart] = useState({ x: 0, y: 0 })
  const [position, setPosition] = useState({ x: 0, y: 0 })
  const [isDragging, setIsDragging] = useState(false)

  const handleDragStart = (e: React.MouseEvent | React.TouchEvent) => {
    setIsDragging(true)
    const clientX = "touches" in e ? e.touches[0].clientX : e.clientX
    const clientY = "touches" in e ? e.touches[0].clientY : e.clientY
    setDragStart({ x: clientX, y: clientY })
  }

  const handleDragMove = (e: MouseEvent | TouchEvent) => {
    if (!isDragging) return

    const clientX = "touches" in e ? e.touches[0].clientX : e.clientX
    const clientY = "touches" in e ? e.touches[0].clientY : e.clientY
    
    const deltaX = clientX - dragStart.x
    const deltaY = clientY - dragStart.y

    setPosition({ x: deltaX, y: deltaY })
  }

  const handleDragEnd = () => {
    setIsDragging(false)
    const threshold = 100

    if (position.x > threshold) {
      onSwipe("right")
    } else if (position.x < -threshold) {
      onSwipe("left")
    }

    setPosition({ x: 0, y: 0 })
  }

  useEffect(() => {
    if (isDragging) {
      window.addEventListener("mousemove", handleDragMove)
      window.addEventListener("mouseup", handleDragEnd)
      window.addEventListener("touchmove", handleDragMove)
      window.addEventListener("touchend", handleDragEnd)
    }

    return () => {
      window.removeEventListener("mousemove", handleDragMove)
      window.removeEventListener("mouseup", handleDragEnd)
      window.removeEventListener("touchmove", handleDragMove)
      window.removeEventListener("touchend", handleDragEnd)
    }
  }, [isDragging])

  const rotation = (position.x / window.innerWidth) * 20

  return (
    <Card
      className="relative h-[70vh] w-full max-w-md overflow-hidden bg-zinc-900 text-white shadow-xl"
      style={{
        transform: `translate(${position.x}px, ${position.y}px) rotate(${rotation}deg)`,
        transition: isDragging ? "none" : "transform 0.5s ease",
        cursor: isDragging ? "grabbing" : "grab",
      }}
      onMouseDown={handleDragStart}
      onTouchStart={handleDragStart}
    >
      <div className="absolute inset-0">
        <img
          src={profile.image}
          alt={profile.name}
          className="h-full w-full object-cover"
        />
        <div className="absolute bottom-0 w-full bg-gradient-to-t from-black/80 to-transparent p-6">
          <h2 className="text-2xl font-bold">
            {profile.name}, {profile.age}
          </h2>
          <p className="mt-2 text-sm text-zinc-300">{profile.bio}</p>
        </div>
      </div>
      <div className="absolute bottom-6 left-1/2 flex -translate-x-1/2 transform gap-4">
        <Button
          size="lg"
          className="h-14 w-14 rounded-full bg-red-500 p-0 hover:bg-red-600"
          onClick={() => onSwipe("left")}
        >
          <X className="h-6 w-6" />
        </Button>
        <Button
          size="lg"
          className="h-14 w-14 rounded-full bg-green-500 p-0 hover:bg-green-600"
          onClick={() => onSwipe("right")}
        >
          <Heart className="h-6 w-6" />
        </Button>
      </div>
    </Card>
  )
}
