export default function SwipePage() {
  const router = useRouter()
  const [profiles, setProfiles] = useState(sampleProfiles)
  const [matches, setMatches] = useState<string[]>([])

  const handleSwipe = (direction: "left" | "right") => {
    if (profiles.length > 0) {
      const currentProfile = profiles[0]
      
      // If swiped right, add to matches
      if (direction === "right") {
        setMatches(prev => [...prev, currentProfile.id])
        // In a real app, you would make an API call here to record the match
      }

      // Remove the current profile from the stack
      setProfiles(prev => prev.slice(1))
    }
  }

  const handleLogout = () => {
    // In a real app, you would handle logout logic here
    router.push("/login")
  }

  return (
    <div className="min-h-screen bg-black">
      {/* Header */}
      <header className="fixed top-0 z-10 w-full bg-zinc-900/80 backdrop-blur-sm">
        <div className="container mx-auto flex items-center justify-between p-4">
          <Link href="/profile">
            <Button variant="ghost" size="icon" className="text-white">
              <User className="h-6 w-6" />
            </Button>
          </Link>
          <Link href="/chat">
            <Button variant="ghost" size="icon" className="text-white">
              <MessageCircle className="h-6 w-6" />
              {matches.length > 0 && (
                <span className="absolute right-0 top-0 h-4 w-4 rounded-full bg-green-500 text-xs">
                  {matches.length}
                </span>
              )}
            </Button>
          </Link>
          <Button variant="ghost" size="icon" className="text-white" onClick={handleLogout}>
            <LogOut className="h-6 w-6" />
          </Button>
        </div>
      </header>

      {/* Main content */}
      <main className="container mx-auto flex min-h-screen items-center justify-center p-4 pt-20">
        {profiles.length > 0 ? (
          <SwipeCard profile={profiles[0]} onSwipe={handleSwipe} />
        ) : (
          <div className="text-center text-white">
            <h2 className="text-2xl font-bold">Plus personne dans les environs</h2>
            <p className="mt-2 text-zinc-400">Revenez plus tard pour voir de nouveaux profils</p>
          </div>
        )}
      </main>
    </div>
  )
}
